<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;企业信息
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" :rules="rules" label-width="100px">
          <el-form-item label="企业名称" prop="name">
            <el-input  placeholder="请输入企业名称" v-model="form.name" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="logo" prop="name">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreview"
              :on-success="handleAvatarSuccess">
              <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="企业地址" prop="address">
            <el-input  placeholder="请输入企业地址" v-model="form.address" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="企业电话" prop="phone">
            <el-input  placeholder="请输入企业电话" v-model="form.phone" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="企业邮箱" prop="email">
            <el-input  placeholder="请输入企业邮箱" v-model="form.email" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="企业简介" prop="introduction">
            <el-input type="textarea"  placeholder="请输入企业简介" v-model="form.introduction" style="width: 40%"></el-input>
          </el-form-item>
          <el-form-item label="微信二维码" prop="wxgzh">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="image"
              :show-file-list="false"
              :on-preview="handlePictureCardPreview2"
              :on-success="handleAvatarSuccess2">
              <img v-if="imageUrl2" :src="img+imageUrl2" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <el-button style="margin-left: 100px"  type="primary" @click="sub('form')">提交</el-button>
        <el-button style="margin-left: 100px"  type="" @click="$router.go(-1)">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'
  export default {
    name: "qyinfo",
    data:function () {
      return{
        type:0,
        img:window.HOST+'/',
        form:{

        },
        rules:{
          address:[{
            required: true,
            message: '请输入企业地址',
            trigger: 'blur'
          }],
          phone:[{
            required: true,
            message: '请输入企业电话',
            trigger: 'blur'
          }],
          introduction:[{
            required: true,
            message: '请输入企业简介',
            trigger: 'blur'
          }],
          name:[{
            required: true,
            message: '请输入企业名称',
            trigger: 'blur'
          }]
        },
        uploadUrl:window.file,
        imageUrl:'',
        imageUrl2:''
      }
    },
    created() {
      this.getDetails()
      // this.getBrandList();
    },
    activated(){
      this.getDetails()
    },
    methods:{
      //详情
      getDetails(){
        var that = this;
        this.apiPost('/admin/showqyinfo',{}).then((res)=>{
          this.handelResponse(res,(data)=>{
            that.form = data.data;
            that.imageUrl = that.form.logo;
            that.imageUrl2 = that.form.mpic;
          })
        })
      },
      sub(formName){
        var that = this;
        this.$refs[formName].validate((valid) => {
            if(valid) {
              if (that.imageUrl2==null){
                that.$message.error('请上传微信二维码');
                return false;
              } else {
                var params = {
                  name:that.form.name,
                  address:that.form.address,
                  phone:that.form.phone,
                  email:that.form.email,
                  introduction:that.form.introduction,
                  wxgzh:that.form.wxgzh,
                  mpic:that.imageUrl2,
                  // longitude:经度
                  // latitude:纬度
                  // telpic:界面顶部电话图片
                  logo:that.imageUrl,
                  // mintelpic:界面顶部电话图片缩略图(方便查看,可不写)
                  // minlogo:logo(方便查看,可不写)
                };
                that.apiPost('/admin/setqyinfo', params).then((res) => {
                  this.handelResponse(res, (data) => {
                    that.$message.success('成功');
                    // that.$router.go(-1);
                    that.getDetails()
                  })
                })
              }
            }
        })
      },
      //上传图片
      handlePictureCardPreview(file) {
        this.form.imgUrl = file.url;
        this.imageUrl = file.url;
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data;
        this.formAdd.imgUrl = res.data;
        // this.imageUrl = this.form.bPic;
      },
      // beforeAvatarUpload(file) {
      //   const isJPG = file.type == 'image/jpeg' || 'image/png';
      //   const isLt2M = file.size / 1024 / 1024 < 2;
      //
      //   /*if(!isJPG) {
      //   	this.$message.error('上传正确格式图片!');
      //   }*/
      //   if (!isLt2M) {
      //     this.$message.error('上传图片大小不能超过 2MB!');
      //   }
      //   return isLt2M;
      // },
      //上传图片
      handlePictureCardPreview2(file) {
        this.form.imgUrl = file.url;
        this.imageUrl2 = file.url;
      },
      handleAvatarSuccess2(res, file) {
        this.imageUrl2 = res.data;
        this.formAdd.imgUrl = res.data;
        // this.imageUrl = this.form.bPic;
      },
      // beforeAvatarUpload2(file) {
      //   const isJPG = file.type == 'image/jpeg' || 'image/png';
      //   const isLt2M = file.size / 1024 / 1024 < 2;
      //
      //   /*if(!isJPG) {
      //   	this.$message.error('上传正确格式图片!');
      //   }*/
      //   if (!isLt2M) {
      //     this.$message.error('上传图片大小不能超过 2MB!');
      //   }
      //   return isLt2M;
      // },
    },
    mixins:[http]
  }
</script>

<style scoped>
  .el-upload--text {
    width: 180px;
  }
  .form-box{
    width: 100%;
  }
</style>



